<template>
    <div class="screen">
        <div class="w-1/2 h-1/2 absolute top-1/2 left-1/2 -translate-1/2 rounded-full border-16 border-white overflow-hidden">
            <video src="../assets/inflate-goal.mp4"
                   muted
                   playsinline
                   preload="metadata"
                   class="w-full h-full absolute top-0 left-0 object-cover"
                   ref="video"
                   @play="videoPlaying = true"
                   @pause="videoPlaying = false">
            </video>
            <div class="w-full h-full flex flex-col justify-center absolute top-0 left-0 p-40 bg-purple-50 text-white text-center"
                 v-show="!videoPlaying">
                <div class="flex items-end -mx-8 mb-16">
                    <div class="w-1/3 px-8">
                        <img src="../assets/icons/chat.svg" alt="">
                    </div>
                    <div class="w-1/3 px-8">
                        <img src="../assets/icons/avatar.svg" alt="">
                    </div>
                    <div class="w-1/3 px-8">
                        <img src="../assets/icons/gift.svg" alt="">
                    </div>
                </div>
                <p class="mb-2 text-5xl leading-none">Commentez, apparaissez...</p>
                <p class="font-black text-12xl text-shadow-red-500 leading-none uppercase">Gagnez !</p>
            </div>
        </div>
        <div v-for="(stage, stageIndex) in stages"
             class="w-64 h-64 absolute top-1/2 left-1/2 -mt-32 -ml-32"
             :style="outerTransform(stageIndex)">
            <avatar :is-winner="!!findWinner(stage)"
                    class="w-full h-full flex justify-center items-center"
                    :class="stageClass(stageIndex)"
                    :style="innerTransform(stageIndex)">
                <span class="text-12xl text-white text-shadow-red-500">{{ stageIndex + 1 }}</span>
                <image-loop :images="stage.comments.map(comment => comment.avatar)"
                            image-class="w-full h-full rounded-full"
                            class="w-full h-full absolute top-0 left-0"
                            v-if="stageIndex === activeStageIndex && !stage.winner">
                </image-loop>
                <img v-if="findWinner(stage)"
                     :src="findWinner(stage).avatar"
                     :alt="findWinner(stage).name"
                     class="w-full h-full absolute top-0 left-0 rounded-full object-cover">
            </avatar>
        </div>
    </div>
</template>

<script>
    import Avatar from './Avatar';
    import ImageLoop from './ImageLoop';

    export default {
        name: 'GameScreen',
        components: {Avatar, ImageLoop},
        props: {
            activeStageIndex: {
                type: Number,
                default: 0,
            },
            stages: {
                type: Array,
                default: () => [],
            },
        },
        data: () => ({
            videoPlaying: false,
        }),
        computed: {
            activeStage() {
                return this.stages[this.activeStageIndex];
            },
        },
        methods: {
            outerTransform(i) {
                return {
                    transform: `rotate(${360 / this.stages.length * i - 90}deg) translateX(700px)`,
                };
            },
            innerTransform(i) {
                return {
                    transform: `rotate(${-360 / this.stages.length * i + 90}deg)`,
                };
            },
            stageClass(i) {
                return {
                    'border-red-500': i === this.activeStageIndex,
                    'border-white': i !== this.activeStageIndex,
                    'opacity-50': i > this.activeStageIndex,
                };
            },
            playVideo() {
                this.$refs.video.play();
                return new Promise(resolve => {
                    this.$refs.video.addEventListener('ended', () => {
                        this.$refs.video.currentTime = 0;
                        resolve();
                    }, {once: true});
                });
            },
            findWinner(stage) {
                return stage.comments.find(comment => comment.isWinner);
            }
        },
    };
</script>

