import faker from 'faker';
import {camelToKebab, generateArray, timeout} from './helpers';

/**
 * @method
 * @name Kipsta#setStages
 * @param {Array} stages
 */
/**
 * @method
 * @name Kipsta#setWinnerScreenDuration
 * @param {number} duration
 */
/**
 * @method
 * @name Kipsta#setCommentIsWinner
 * @param {string} id
 * @param {boolean} isWinner
 */

/**
 * @method
 * @name Kipsta#addComment
 * @param {Object} comment
 */
/**
 * @method
 * @name Kipsta#removeComment
 * @param {string} id
 */

/**
 * @method
 * @name Kipsta#showGameScreen
 */
/**
 * @method
 * @name Kipsta#showWinnersScreen
 */

export class Kipsta {

    constructor(vm) {
        this._vm = vm;

        return new Proxy(this, {
            get(target, prop) {
                if (prop in target) {
                    return target[prop];
                }

                return (...args) => {
                    target._vm.$emit(camelToKebab(prop), ...args);
                };
            },
        });
    }

    async demo() {
        console.log('set stages');
        let stages = generateArray(11).map(() => ({ // only for demo
            winner: null,
            comments: [],
        }));
        this.setStages(stages);                                     // only for demo
        this.setWinnerScreenDuration(3000);                         // only for demo, set to 30000 by default

        await timeout(2000);
        console.log('show game screen');
        this.showGameScreen();

        for (let [i, stage] of stages.entries()) {
            let comments = generateArray(faker.random.number({min: 2, max: 6})).map(() => ({
                id: faker.random.uuid(),
                name: faker.name.findName(),
                avatar: faker.internet.avatar(),
                isWinner: false,
            }));
            console.log(`add comments to stage ${i + 1}`, comments);
            comments.forEach(comment => this.addComment(comment));
            await timeout(5000);

            console.log(`add winner to stage ${i + 1}`);
            this.setCommentIsWinner(faker.random.arrayElement(comments).id, true);
            await timeout(10000);
        }

        console.log('show winners screen');
        this.showWinnersScreen();
    }

}