<template>
    <div id="app" class="relative">
        <video src="./assets/background-animation.mp4"
               autoplay
               loop
               muted
               playsinline
               preload="metadata"
               class="w-full h-full absolute inset-0 -z-1">
        </video>
        <div class="w-full absolute bottom-0 left-0 z-20 px-16 py-6 bg-red-500">
            <img src="./assets/kipsta-logo.svg" alt="Kipsta" id="kipsta-logo">
        </div>

        <fade-transition>
            <start-screen v-show="screen === 'StartScreen'"></start-screen>
        </fade-transition>
        <fade-transition>
            <game-screen :active-stage-index="activeStageIndex"
                         :stages="stages"
                         ref="gameScreen"
                         v-show="screen === 'GameScreen'">
            </game-screen>
        </fade-transition>
        <fade-transition>
            <winners-screen :winners="winners" v-show="screen === 'WinnersScreen'"></winners-screen>
        </fade-transition>
        <fade-transition>
            <winner-screen v-show="winnerScreenVisible" :winner="winner"></winner-screen>
        </fade-transition>
    </div>
</template>

<script>
    import FadeTransition from './components/FadeTransition';
    import GameScreen from './components/GameScreen';
    import StartScreen from './components/StartScreen';
    import WinnerScreen from './components/WinnerScreen';
    import WinnersScreen from './components/WinnersScreen';
    import {generateArray, timeout} from './helpers';

    let winnerScreenDuration = 30000; // 30s

    export default {
        name: 'App',
        components: {FadeTransition, GameScreen, StartScreen, WinnerScreen, WinnersScreen},
        data: () => ({
            screen: StartScreen.name,
            winnerScreenVisible: false,
            activeStageIndex: 0,
            stages: generateArray(11).map(() => ({
                winner: null,
                comments: [],
            })),
        }),
        computed: {
            activeStage() {
                return this.stages[this.activeStageIndex] || null;
            },
            winner() {
                return this.activeStage ? this.activeStage.comments.find(comment => comment.isWinner) || {} : {};
            },
            winners() {
                return this.stages.map(stage => stage.comments.find(comment => comment.isWinner)).filter(winner => !!winner);
            },
        },
        methods: {
            findCommentIndex(id) {
                return this.activeStage.comments.findIndex(comment => comment.id === id);
            },
            findComment(id) {
                return this.activeStage.comments.find(comment => comment.id === id);
            },
            setWinner(id, isWinner) {
                let comment = this.findComment(id);
                if (comment) {
                    comment.isWinner = isWinner;
                }
            },
        },
        mounted() {
            this.$root.$on('set-stages', stages => this.stages = stages);
            this.$root.$on('set-winner-screen-duration', duration => winnerScreenDuration = duration);
            this.$root.$on('show-game-screen', () => this.screen = GameScreen.name);
            this.$root.$on('show-winners-screen', () => this.screen = WinnersScreen.name);
            this.$root.$on('add-comment', comment => {
                this.activeStage.comments.push(comment);
            });
            this.$root.$on('remove-comment', id => {
                let index = this.findCommentIndex(id);
                if (index !== -1) {
                    this.activeStage.comments.splice(index, 1);
                }
            });
            this.$root.$on('set-comment-is-winner', async (id, isWinner) => {
                this.setWinner(id, isWinner);
                await this.$refs.gameScreen.playVideo();
                this.winnerScreenVisible = true;
                await timeout(winnerScreenDuration);
                this.winnerScreenVisible = false;
                await timeout(300);
                this.activeStageIndex++;
            });
        },
    };
</script>

<style src="./main.css"></style>
