<template>
    <div>
        <img v-for="(image, i) in images"
             v-show="i === activeImage"
             :src="image"
             alt=""
             class="absolute top-1/2 left-1/2 -translate-1/2"
             :class="imageClass">
    </div>
</template>

<script>
    export default {
        name: 'ImageLoop',
        props: {
            images: {
                type: Array,
                default: () => [],
            },
            imageClass: {
                type: [String, Array, Object],
                default: () => '',
            },
            interval: {
                type: Number,
                default: 2000,
            },
        },
        data: () => ({
            activeImage: 0,
        }),
        methods: {
            start() {
                if (this.intervalId) {
                    this.stop();
                }
                this.intervalId = setInterval(() => this.activeImage = (this.activeImage + 1) % this.images.length, this.interval);
            },
            stop() {
                if (this.intervalId) {
                    clearInterval(this.intervalId);
                    this.intervalId = null;
                }
            },
        },
        watch: {
            images() {
                if (this.images.length > 1) {
                    this.start();
                } else {
                    this.stop();
                }
            },
        },
        mounted() {
            if (this.images.length > 1) {
                this.start();
            }
        },
        beforeDestroy() {
            this.stop();
        },
    };
</script>
