<template>
    <div class="screen">
        <p class="mt-16 mb-32 font-black text-6xl text-center text-white uppercase">
            Merci à tous d’avoir participé <br> Félicitations aux gagnants !
        </p>
        <div v-for="(group, i) in groupedWinners" class="flex justify-center -mx-10 mb-16">
            <div v-for="(winner, j) in group"
                 class="px-10"
                 :class="{'-mt-20': (i === 1 || i === 2) && (j === 0 || j === 3)}">
                <avatar is-winner class="w-64 h-64 mx-auto relative">
                    <img :src="winner.avatar"
                         :alt="winner.name"
                         class="w-full h-full absolute top-0 left-0 rounded-full object-cover">
                </avatar>
                <div class="w-96 flex justify-center items-center">
                    <p class="text-4xl text-white whitespace-no-wrap">{{ winner.name }}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Avatar from './Avatar';

    export default {
        name: 'WinnersScreen',
        components: {Avatar},
        props: {
            winners: {
                type: Array,
                default: () => [],
            },
        },
        computed: {
            groupedWinners() {
                return [
                    this.winners.slice(0, 2),
                    this.winners.slice(2, 6),
                    this.winners.slice(6, 10),
                    this.winners.slice(10),
                ];
            },
        },
    };
</script>

<style scoped>
    .screen {
        background-image: url("../assets/football-lines.png"), url("../assets/background.jpg");
        background-repeat: no-repeat, no-repeat;
        background-position: bottom, center;
        background-size: auto, cover;
    }
</style>
